var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("谜语人")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        谜语（爱德华Nigma或Nygma）是一个虚构的超级大出现在发表漫画书DC漫画，通过创造比尔手指和迪克窜出。他最初出现在侦探漫画＃140（1948年10月）。这个角色通常被描述为高谭市的犯罪策划者，他喜欢将谜语和谜题融入他的计划中，让他们成为当局解决的线索。Riddler是超级英雄蝙蝠侠最持久的敌人之一，属于构成他的盗贼画廊的敌人的集体。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        2009年，Riddler被评为IGN历史上第59位最伟大的漫画小人。角色已经从漫画中大幅改编成各种形式的媒体，包括故事片，电视连续剧和电子游戏。迷哥已经被讲约翰·格洛弗在DC生气蓬勃的宇宙，罗伯特·英格伦在蝙蝠侠和沃利·温格特在阿卡姆：蝙蝠侠视频游戏系列。他被刻画了由真人弗兰克·戈舍和约翰阿斯廷在20世纪60年代蝙蝠侠电视系列节目，吉姆·凯瑞在1995年的电影蝙蝠侠永远，和科里迈克尔史密斯在FOX电视节目 Gotham。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Riddler对谜语，谜题和文字游戏有着迷恋。这个角色经常喜欢过度陈述他的“智力优势”，并通过向他们发送复杂的线索预警蝙蝠侠和警察。他的名字是双关语，Enigma（Edward Nygma），意思是一个神秘或难以理解的人或事物。由于这种噱头的自觉使用，Riddler的罪行是华丽和炫耀。这个角色被描绘成戴着多米诺面具，带有绿色西装和礼帽，或带有问号印记的绿色单人。黑色，绿色或紫色问号 作为他的视觉主题。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        角色的起源故事讲述了Riddler，其真名是Edward Nigma（Edward Nashton，或者是后来的作家Nygma），在年轻时就迷上了谜题。在老师宣布将举行一场能够最快解决难题的比赛之后，Nigma将目光投向了赢得这场比赛，渴望胜利带来的荣耀和满足感。一天晚上，他偷偷溜进学校，从教师的桌子上拿出这个谜题，并练习它，直到他能在一分钟内解决它。正如预测的那样，他赢得了比赛，并获得了一本关于谜语作为奖品的书。他的作弊得到了回报，爱德华接受了各种谜题的掌握，最终成为一名狂热的员工，擅长用他奇怪的谜题和心灵游戏欺骗客户的钱。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'